body {
  text-align: center;
  background-color: #f4f4f4;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

h1 {
  color: #fff;
  background-color: #1c5a8d;
  margin-top: 0;
  padding: 10px;
}

.container {
  max-width: 400px;
  margin: 20px auto;
}

ul {
  padding: 0;
  list-style: none;
}

label {
  display: block;
}

input[type="text"] {
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 70%;
  margin-right: 5px;
  padding: 10px;
}

select {
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 74%;
  margin: 10px 0;
  padding: 10px;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #1c5a8d;
  border: none;
  border-radius: 4px;
  padding: 10px 15px;
}

button:hover {
  background-color: #023d77;
}

input[type="number"] {
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 50px;
  margin-right: 5px;
  padding: 8px;
}

#bookList li {
  border: 1px solid #ddd;
  border-radius: 8px;
  margin-bottom: 10px;
  padding: 12px;
  list-style: none;
  box-shadow: 0 2px 5px #0000001a;
}

#bookList h3 {
  margin: 0 0 8px;
}

#bookList p {
  margin: 4px 0;
}

.book-rating-input {
  align-items: center;
  margin-top: 8px;
  display: flex;
}

.book-rating-input button {
  margin-left: 10px;
}

#chatbot-container {
  background: #fff;
  border: 1px solid #ccc;
  width: 300px;
  padding: 10px;
  position: fixed;
  bottom: 20px;
  right: 20px;
  box-shadow: 0 4px 6px #0000001a;
}

#chat-history {
  border-bottom: 1px solid #ccc;
  height: 200px;
  margin-bottom: 10px;
  padding: 5px;
  overflow-y: auto;
}

#chat-input {
  width: calc(100% - 60px);
  margin-right: 5px;
  padding: 5px;
}

#send-btn {
  cursor: pointer;
  padding: 5px 10px;
}

.history {
  padding-bottom: 15px;
}
